/* Home Section */
.home {
  position: relative;
  width: 100%;
  height: 100vh; /* Full screen height */
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
}

/* Background Image */
.background-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -2;
}

/* Gradient Overlay */
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.95), rgba(255, 255, 255, 0.05));
  z-index: -1;
}

/* Home Content */
.home-content {
  position: relative;
  max-width: 600px;
  padding: 0 10%;
  color: white;
  z-index: 1;
}

.home-content h1 {
  font-size: 3.5rem;
  font-weight: bold;
  margin-bottom: 15px;
}

.home-content h2 {
  font-size: 1.8rem;
  color: #00e5ff;
  margin-bottom: 20px;
}

.home-content p {
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 30px;
}

/* Buttons */
.home-buttons {
  display: flex;
  gap: 15px;
}

.home-buttons a {
  padding: 12px 25px;
  border: 2px solid #00e5ff;
  border-radius: 25px;
  color: #00e5ff;
  text-decoration: none;
  font-weight: bold;
  transition: all 0.3s ease-in-out;
}

.home-buttons a:hover {
  background-color: #00e5ff;
  color: #000;
}

/* Tablet View */
@media screen and (max-width: 1024px) {
  .home-content {
    padding: 0 5%;
    max-width: 500px;
  }

  .home-content h1 {
    font-size: 3rem;
  }

  .home-content h2 {
    font-size: 1.6rem;
  }
}

/* Mobile View */
@media screen and (max-width: 768px) {
  .home {
    justify-content: center;
    text-align: center;
    height: 100vh;
    padding: 0 20px;
  }

  .home-content {
    padding: 0;
    max-width: 90%;
  }

  .home-content h1 {
    font-size: 2.5rem;
  }

  .home-content h2 {
    font-size: 1.4rem;
  }

  .home-buttons {
    justify-content: center;
  }
}
